<template>
  <div>
    <el-row>
      <el-col :xs="24" :md="12" class="col-12">
        <el-row :gutter="20">
          <el-col :span="15">
            <el-input v-model="token" placeholder="Please token" />
          </el-col>
          <el-col :span="8">
            <el-button
              type="success"
              icon="el-icon-turn-off"
              @click="searchUser"
              round
              >人员</el-button
            >
            <el-button
              type="success"
              icon="el-icon-turn-off"
              @click="handleStart"
              round
              >开始</el-button
            >
          </el-col>
        </el-row>
        <el-row :gutter="20">
          <el-col :span="10">
            <el-select
              v-model="province"
              @change="changeProvince"
              placeholder="省"
              style="width: 100%"
            >
              <el-option
                v-for="item in provinceList"
                :key="item.value"
                :label="item.name"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-col>
          <el-col :span="10">
            <el-select v-model="city" placeholder="市" style="width: 100%">
              <el-option
                v-for="item in cityList"
                :key="item.value"
                :label="item.name"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-col>
          <el-col :span="4">
            <el-button
              type="success"
              icon="el-icon-search"
              @click="handleSearch"
              round
              >查询</el-button
            >
          </el-col>
        </el-row>
      </el-col>
      <el-col :xs="24" :md="12" class="col-12">
        <el-table
          :data="userList"
          highlight-current-row
          @current-change="handleCurrentChange"
          border
          style="width: 100%"
        >
          <el-table-column prop="id" label="id" minWidth="100" align="center" />
          <el-table-column
            prop="name"
            label="姓名"
            minWidth="100"
            align="center"
          />
          <el-table-column
            prop="address"
            label="地址"
            minWidth="130"
            align="center"
          />
          <el-table-column
            prop="idCardNo"
            label="身份证"
            minWidth="130"
            align="center"
          />
        </el-table>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="24" align="left" style="margin-bottom: 10px">
        <el-switch
          v-model="isSubscribe"
          inactive-text="是否订阅"
          style="margin-right: 10px"
        />
        <el-switch v-model="isMake" inactive-text="是否预约" />
      </el-col>
      <el-col :span="24">
        <el-table
          v-loading="loading"
          :data="tableData"
          border
          style="width: 100%"
        >
          <el-table-column type="selection" width="55" align="center" />
          <el-table-column
            prop="isNoticedUserAllowed"
            label="是否可预约"
            minWidth="70"
            align="center"
          >
            <template #default="{ row }">
              {{
                row.isNoticedUserAllowed === 0 && row.total === 1
                  ? '可以'
                  : '不行'
              }}
            </template>
          </el-table-column>
          <el-table-column
            prop="total"
            label="是否可订阅"
            minWidth="70"
            align="center"
          >
            <template #default="{ row }">
              {{ row.total === 0 ? '不行' : '可以' }}
            </template>
          </el-table-column>
          <el-table-column
            prop="name"
            label="医院名称"
            minWidth="130"
            align="center"
          />
          <el-table-column
            prop="vaccineName"
            label="疫苗名称"
            minWidth="130"
            align="center"
          />
          <el-table-column
            prop="address"
            label="地址"
            minWidth="130"
            align="center"
          />
          <el-table-column
            prop="worktimeDesc"
            label="建议"
            minWidth="130"
            align="center"
          />
        </el-table>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  name: 'yueMiao',
  data() {
    return {
      loading: false,
      isSubscribe: true,
      isMake: true,
      tableData: [],
      provinceList: [],
      province: '42',
      cityList: [],
      city: '4201',
      token: '',
      userId: 9566067,
      userList: [],
      user: '',
      days: []
    }
  },
  methods: {
    handleStart() {
      if (this.token === '') {
        this.$message.error('请抓取token')
        return
      }
      this.tableData.forEach((item) => {
        if (
          item.total === 1 &&
          item.isNoticedUserAllowed === 0 &&
          this.isMake === true
        ) {
          // eslint-disable-next-line no-extra-semi
          ;(async function (that) {
            const item1 = await that.$axios({
              method: 'get',
              headers: {
                tk: that.token
              },
              params: { id: item.depaVaccId },
              url: '/getItem'
            })

            if (item1.data.code == '0000') {
              Object.assign(item, item1.data.data)
              // const item2 = await that.$axios({
              //   method: 'get',
              //   headers:{
              //     tk:that.token
              //   },
              //   params:{
              //     depaCode:item.departmentCode,
              //     vaccCode:item.vaccineCode,
              //     linkmanId:that.user.id,//用户id
              //     vaccIndex:1,
              //     departmentVaccineId:item.departmentVaccineId,
              //     month:'2021-10-01',
              //   },
              //   url: '/getDay',
              // })
              // console.log('item2: ', item2);
              const item3 = await that.$axios({
                method: 'get',
                headers: {
                  tk: that.token
                },
                params: {
                  depaCode: item.departmentCode,
                  days: String(that.days),
                  vaccCode: item.vaccineCode,
                  vaccIndex: 1,
                  departmentVaccineId: item.departmentVaccineId
                },
                url: '/getIf'
              })
              console.log('item3: ', item3)
            } else {
              this.$message.error(item1.data.msg)
            }
          })(this)
          return
        }
        if (item.total === 1 && this.isSubscribe === true) {
          this.$axios({
            method: 'get',
            headers: {
              tk: this.token
            },
            params: { id: item.depaVaccId },
            url: '/getItem'
          }).then((res) => {
            if (res.data.code == '0000') {
              Object.assign(item, res.data.data)
              this.$axios({
                method: 'get',
                headers: {
                  tk: this.token
                },
                params: {
                  vaccineCode: item.vaccineCode,
                  productId: item.productId,
                  depaVaccId: item.depaVaccId,
                  linkmanId: this.user.id, //用户id
                  idCardNo: this.user.idCardNo //身份证
                },
                url: '/checkLinkman'
              }).then((res) => {
                if (res.data.code == '0000') {
                  console.log(res.data)
                } else {
                  this.$message.error(res.data.msg)
                }
              })
            } else {
              this.$message.error(res.data.msg)
            }
          })
        }
      })
    },
    searchUser() {
      if (this.token === '') {
        this.$message.error('请抓取token')
        return
      }
      this.getUser()
    },
    getList() {
      this.loading = true
      this.$axios({
        method: 'post',
        data: `offset=0&limit=100&name=&regionCode=${this.city}&isOpen=1&longitude=114.1950912475586&latitude=30.649051666259766&sortType=1&vaccineCode=&customId=3`,
        url: '/getDepartments'
      }).then((res) => {
        this.loading = false
        this.tableData = res.data.data.rows
      })
    },
    getUser() {
      this.$axios({
        method: 'get',
        headers: {
          tk: this.token
        },
        params: {
          id: this.userId,
          isShowDescribtion: true,
          showOthers: true
        },
        url: '/getUser'
      }).then((res) => {
        if (res.data.code == '0000') {
          this.userList = res.data.data
        } else {
          this.$message.error(res.data.msg)
        }
      })
    },
    getProvince() {
      this.$axios({
        method: 'get',
        params: { parentCode: '' },
        url: '/getProvince'
      }).then((res) => {
        this.provinceList = res.data.data
      })
    },
    getCity(val) {
      this.$axios({
        method: 'get',
        params: { parentCode: val },
        url: '/getCity'
      }).then((res) => {
        this.cityList = res.data.data
      })
    },
    changeProvince(val) {
      this.cityList = []
      this.city = ''
      this.getCity(val)
    },
    handleSearch() {
      this.getList()
    },
    handleCurrentChange(user) {
      this.user = user
    }
  },
  mounted() {
    let day = this.$moment().format('YYYY-MM-DD')
    for (let i = 0; i < 150; i++) {
      const days = this.$moment(day).add(1, 'days')
      day = days
      this.days.push(this.$moment(days).format('YYYY-MM-DD'))
    }
    this.getCity('42')
    this.getProvince()
  }
}
</script>

<style>
.el-row {
  margin-bottom: 20px;
}
.el-row:last-child {
  margin-bottom: 0;
}
.col-12 {
  padding: 20px 10px 10px 10px;
}
</style>