<template>
  <div>
    <el-row :gutter="20" align="bottom" justify="center">
      <el-col :span="10">
        <el-input v-model="token" placeholder="Please token" />
      </el-col>
      <el-col :span="10">
        <el-input v-model="classId" placeholder="课程ID" />
      </el-col>
      <el-col :span="2" align="left">
        <el-button
          icon="el-icon-check"
          type="primary"
          round
          @click="handleStart"
        >
          查询
        </el-button>
      </el-col>
    </el-row>
    <el-row style="padding: 0 150px">
      <el-col :span="24" align="center" style="margin-bottom: 10px">
        <el-table
          :data="data"
          v-loading="loading"
          :row-class-name="tableRowClassName"
          show-summary
          height="600"
          style="width: 100%"
          border
        >
          <el-table-column
            prop="businessName"
            label="名称"
            min-width="500"
            align="center"
          />
          <el-table-column
            prop="name"
            label="上级目录名称"
            min-width="210"
            align="center"
          />
          <el-table-column
            prop="businessId"
            label="地址"
            min-width="310"
            align="center"
          />
          <el-table-column label="状态" width="120" align="center">
            <template #default="{ row }">
              {{ row.finishStatus == 1 ? '已学习' : '未完成' }}
            </template>
          </el-table-column>
        </el-table>
        <el-alert
          :title="tips"
          effect="dark"
          type="success"
          :closable="false"
        />
      </el-col>
    </el-row>
    <el-row style="padding: 0 150px">
      <el-col :offset="8" :span="4" align="center">
        <el-input-number v-model="num" :step="1" />
      </el-col>
      <el-col :span="4" align="center">
        <el-button
          type="primary"
          icon="el-icon-video-camera"
          @click="handlePlay"
          round
        >
          播放
        </el-button>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  name: 'BrushClass',
  data() {
    return {
      data: [],
      timer: null,
      loading: false,
      video: [],
      videoList: [],
      num: 4,
      finishStatusList: [],
      tips: '',
      token: 'Bearer__091d318a0671ccef2ded55eea7ea5bad',
      classId: 'f96d2eff-3ab1-4da9-9aff-0aaa097b13b3'
    }
  },
  methods: {
    //加载数据
    async handleStart() {
      this.loading = true
      this.data = []
      this.videoList = []
      const idList = await this.$axios({
        method: 'get',
        headers: {
          Authorization: this.token
        },
        params: { classId: this.classId },
        url: '/getCourseList'
      }).catch((err) => {
        this.$message({
          message:
            err.response.data.message == 'Access is Denied'
              ? 'token 过期'
              : err.response.data.message,
          type: 'error'
        })
      })
      this.data = await new Promise((resolve) => {
        let data = [],
          idx = 0
        idList.data.forEach((item) => {
          this.$axios({
            method: 'get',
            headers: {
              Authorization: this.token
            },
            params: {
              classId: this.classId,
              chapterId: item.id,
              page: 1,
              pageSize: 10000
            },
            url: '/getDetailCourseList'
          }).then((res) => {
            data = [
              ...data,
              ...res.data.items.map((item1) => {
                return {
                  ...item1,
                  name: item.name,
                  finishStatus: item1.classStudentActivityProgress.finishStatus
                }
              })
            ]
            idx++
            if (idx === idList.data.length) {
              resolve(data)
            }
          })
        })
      })
      this.loading = false
      this.finishStatusList = []
      this.data.forEach((item) => {
        if (item.finishStatus != 1) {
          //设置未播放完成列表-->videoList
          this.videoList.push(item.businessId)
        } else {
          //设置已播放完成列表-->finishStatusList
          this.finishStatusList.push(item.businessId)
        }
      })
      this.tips = `当前未学习${this.videoList.length}个！已学习${
        this.data.length - this.videoList.length
      }个！`
    },
    //开始播放，定时检测，浏览器窗口版
    async handlePlay() {
      this.$notify({
        title: '警告',
        message: `视频播放中，请勿关闭此页面`,
        type: 'warning',
        showClose: false,
        position: 'top-left',
        duration: 10000
      })
      clearInterval(this.timer)
      this.video = this.videoList.slice(0, this.num)
      this.video.forEach((item) => {
        window.open(
          `https://kc.zhixueyun.com/#/study/course/detail/11&${item}/5/1`,
          ['_blank', item]
        )
      })
      this.timer = setInterval(() => {
        // eslint-disable-next-line no-extra-semi
        this.$notify({
          title: '警告',
          message: `定时更新状态中，请勿关闭此页面`,
          type: 'warning',
          showClose: false,
          position: 'top-left',
          duration: 10000
        })
        this.handleStart().then(() => {
          this.video = this.video.map((item) => {
            if (this.finishStatusList.includes(item)) {
              this.$notify({
                title: '成功',
                message: `已学习完成  【${this.getItemName(item)}】`,
                type: 'success',
                duration: 0
              })
              return null
            }
            return item
          })
          const v = JSON.parse(JSON.stringify(this.video))
          this.video = this.video.map((item) => {
            if (!item) {
              for (let i = 0; i < this.videoList.length; i++) {
                const item = this.videoList[i]
                if (!v.includes(item)) {
                  v.push(item)
                  setTimeout(() => {
                    this.$notify({
                      title: '提示',
                      message: `开始学习  【${this.getItemName(item)}】`,
                      type: 'info',
                      duration: 0
                    })
                  }, 500)
                  window.open(
                    `https://kc.zhixueyun.com/#/study/course/detail/11&${item}/5/1`,
                    ['_blank', item]
                  )
                  return item
                }
              }
            }
            return item
          })
          this.$notify({
            title: '成功',
            message: '定时更新状态成功',
            type: 'success',
            showClose: false,
            position: 'top-left',
            duration: 10000
          })
        })
      }, 1 * 60 * 1000)
    },
    getItemName(id) {
      for (let i = 0; i < this.data.length; i++) {
        const item = this.data[i]
        if (item.businessId === id) {
          return item.businessName
        }
      }
    },
    // 更改样式
    tableRowClassName({ row }) {
      return row.finishStatus == 1 ? 'success-row' : 'warning-row'
    }
  }
}
</script>
<style >
.el-table .warning-row {
  background: #82848a;
}
.el-table .success-row {
  background: #f0f9eb;
}

.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.text {
  font-size: 14px;
}

.item {
  margin-bottom: 18px;
}

.box-card {
  width: 480px;
}
</style>
